html, body {
    width: 100%;
    height: 100%;
}

body {
    background-color: #2E363F;
    padding: 0;
    margin-top: 10%;
}

#logo, #loginbox {
    width: 32%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

#loginbox {
    overflow: hidden !important;
    text-align: left;
    position: relative;
}

#loginform {
    width: 100%;
    background: #fff;
    position: relative;
    top: 0;
    left: 0;
}

#loginform {
    z-index: 200;
    display: block;
}

#recoverform {
    z-index: 100;
    display: none;
}

#loginform .form-group {
    padding: 0 20px;
}

#loginbox .normal_text {
    padding: 15px 10px;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    background: #49CCED;
    color: #fff;
}

.inline-align {
    width: 100%;
    white-space: nowrap;
}

.inline-align-left {
    display: inline-block;
    width: 50%;
    text-align: left;
}

.inline-align-right {
    display: inline-block;
    width: 49%;
    text-align: right;
}

@media (max-width: 480px) {
    #logo {
        width: 40%;
    }

    #loginbox {
        width: 90%;
    }
}

.title {
    text-shadow: 2px 2px 2px rgba(150, 150, 150, 1);
}